<div class="w-full p-3 h-auto">
    <div class="my-2">
        <div class="grid grid-rows-2">
            <div>
                <h3 class="title-left text-4xl my-4 font-bold">About Me</h3>
            </div>
            <div>
                <p>{{description[0]}}</p>
            </div>
        </div>
        <about-me-data-info></about-me-data-info> 
    </div>
</div>
</template>

<script lang="ts">
import store from "@/store";
import {
  Options,
    Vue
} from "vue-class-component";
import AboutMeDataInfo from "./AboutMeDataInfo.vue";
@Options({
    props: {
        resume: String,
    },
    components: {
        "about-me-data-info": AboutMeDataInfo
    }
})
export default class AboutMe extends Vue {

    get description() {
        if (store.state.resume.description)
        return store.state.resume.description
        else return ""
    }
}
</script>

