
import store from "@/store";
import {
    Vue
} from "vue-class-component";
import img from "../../../assets/images/mihaprofile.png"

export default class HeaderInfo extends Vue {
    get fullName() {
        return `${store.state.resume.name} ${store.state.resume.lastname}`;
    }

    get img() {
        return img;
    }

    get descriptionHeader() {
        return `${store.state.resume.descriptionHead}`
    }

}
